

//
function checkEmail(email) {
	return RegExp(/^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/).test(email);
}
//
function checkMobile(mobile){
	return RegExp(/^1[34578]\d{9}$/).test(mobile);
}
//
function isequality(a,b){
	let iszt = false ;
	if(a == b && a != "" && b != ""){
		iszt =  true
	}
	return iszt
}

module.exports = {
	checkEmail, //
	checkMobile,//
	isequality,//
}
