

//js
const utils = {
	// 
	isNullOrEmpty: function(value) {
		return (value === null || value === '' || value === undefined) ? false : true;
	},
	// 
	trim: function(value) {
		return value.replace(/(^\s*)|(\s*$)/g, "");
	},
	// 
	isMobile: function(value) {
		return /^(?:13\d|14\d|15\d|16\d|17\d|18\d|19\d)\d{5}(\d{3}|\*{3})$/.test(value);
	},
	// 
	isFloat: function(value) {
		return /^([0-9]*[.]?[0-9])[0-9]{0,1}$/.test(value);
	},
	// 
	isNum: function(value) {
		return /^[0-9]+$/.test(value);
	},
	// 8~20
	checkPwd: function(value) {
		return /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,20}$/.test(value);
	},
	/**
	 *  
	 * @param {string} num 
	 * @returns 134****2222
	 */ 
	formatNum: function(num) {
		if (utils.isMobile(num)) {
			num = num.replace(/^(\d{3})\d{4}(\d{4})$/, '$1****$2')
		}
		return num;
	},
	/**
	 * 
	 * @param {string | number} money 
	 * @returns { string } "1,255,454.00"
	 */
	rmoney: function(money) {
		return parseFloat(money).toFixed(2).toString().split('').reverse().join('').replace(/(\d{3})/g, '$1,').replace(
			/\,$/, '').split('').reverse().join('');
	},
	// 
	formatDate: function(formatStr, fdate) {
		//
		if (fdate) {
			if (~fdate.indexOf('.')) {
				fdate = fdate.substring(0, fdate.indexOf('.'));
			}
			fdate = fdate.toString().replace('T', ' ').replace(/\-/g, '/');
			var fTime, fStr = 'ymdhis';
			if (!formatStr)
				formatStr = "y-m-d h:i:s";
			if (fdate)
				fTime = new Date(fdate);
			else
				fTime = new Date();
			var month = fTime.getMonth() + 1;
			var day = fTime.getDate();
			var hours = fTime.getHours();
			var minu = fTime.getMinutes();
			var second = fTime.getSeconds();
			month = month < 10 ? '0' + month : month;
			day = day < 10 ? '0' + day : day;
			hours = hours < 10 ? ('0' + hours) : hours;
			minu = minu < 10 ? '0' + minu : minu;
			second = second < 10 ? '0' + second : second;
			var formatArr = [
				fTime.getFullYear().toString(),
				month.toString(),
				day.toString(),
				hours.toString(),
				minu.toString(),
				second.toString()
			]
			for (var i = 0; i < formatArr.length; i++) {
				formatStr = formatStr.replace(fStr.charAt(i), formatArr[i]);
			}
			return formatStr;
		} else {
			return "";
		}
	},
	//  -- 
	toFormatDate: function (value) {
		let date = new Date(value*1000);
		let y = date.getFullYear();
		let MM = date.getMonth() + 1;
		MM = MM < 10 ? ('0' + MM) : MM;
		let d = date.getDate();
		d = d < 10 ? ('0' + d) : d;
		let h = date.getHours();
		h = h < 10 ? ('0' + h) : h;
		let m = date.getMinutes();
		m = m < 10 ? ('0' + m) : m;
		let s = date.getSeconds();
		s = s < 10 ? ('0' + s) : s;
		return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
	},
	// 1w
	numSystemVal: function (val) {
		if(val*1<10000){
			val = (val*1).toFixed(2);
		}else{
			if((val*1)%10000==0){
				val = parseInt((val*1)/10000)+''
			}else{
				val = parseInt((val*1)/10000)+'+'
			}
		}
		return val
	},
	// 1w--(1000010000:1W+)
	numFormatVal: function (val) {
		if(val*1<10000){
			val = val;
		}else{
			if((val*1)%10000==0){
				val = parseInt((val*1)/10000)+''
			}else{
				val = parseInt((val*1)/10000)+'+'
			}
		}
		return val
	},
	
	/**
	 * author hp
	 * msg  
	 */
	isWeChat: function() {







	}, 
	/**
	 * 
	 * res: Obj
	 * res.address
	 * res.name 
	 * */
	getAddrInfo(res) {
		let regex = /^(|||||)/
		let REGION_PROVINCE = []
		let addressBean = {
			REGION_PROVINCE: null,
			REGION_COUNTRY: null,
			REGION_CITY: null,
			ADDRESS: null,
		}
		// 
		function regexAddressBean(address, addressBean) {
			// regex = /^(.*?[]|.*?|.*?)(.*?[])(.*?)$/g;
			regex = /^(.*?[]|.*?|.*?)(.*?[])(.*?)$/g
			let addxress = regex.exec(address)
			addressBean.REGION_CITY = addxress[1] //
			addressBean.REGION_COUNTRY = addxress[2] //
			addressBean.ADDRESS = addxress[3] + res.name //
		}
	
		if (!(REGION_PROVINCE = regex.exec(res.address))) {
			regex = /^(.*?(|))(.*?)$/
			addressBean.REGION_PROVINCE = regex.exec(res.address)[1] //
			regexAddressBean(regex.exec(res.address)[3], addressBean)
		}
		return addressBean
	},
}
